/* Import a clean, professional font from Google */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

/* --- Reset and Base Styles --- */
* {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #1a1c23; /* Dark background */
  color: #e0e0e0; /* Light text */
  margin: 0; /* Remove default body margin */
}

/* --- App Layout --- */
.app-layout {
  display: flex;
}

.main-content {
  flex-grow: 1;
  height: 100vh;
  overflow-y: auto;
  margin-left: 260px; /* Same width as sidebar */
  padding: 0;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  background-color: #23252e; /* Darker than body */
  border-bottom: 1px solid #30333d; /* Subtle border */
  position: sticky;
  top: 0;
  z-index: 10;
}

.main-header h1 {
  font-size: 1.75rem;
  font-weight: 600;
  color: #ffffff;
  margin: 0;
}

.content-area {
  padding: 30px;
}

/* --- Header Search Box --- */
.header-search {
  display: flex;
  align-items: center;
}
.header-search > div { /* Target the react-select container */
  width: 100%;
  min-width: 350px;
}

/* --- General Card Style --- */
.card {
  background-color: #23252e;
  border-radius: 8px;
  border: 1px solid #30333d;
  margin-bottom: 20px;
  color: #e0e0e0;
}

/* --- Spinner --- */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 50px;
}
@keyframes bounce {
  0%, 80%, 100% { transform: scale(0); }
  40% { transform: scale(1.0); }
}
.loading-spinner div {
  width: 10px; height: 10px; background-color: #3498db; border-radius: 50%;
  animation: bounce 1.4s infinite ease-in-out both;
}
.loading-spinner .dot1 { animation-delay: -0.32s; }
.loading-spinner .dot2 { animation-delay: -0.16s; }

/* ========================================= */
/* --- DASHBOARD LAYOUT --- */
/* ========================================= */
.dashboard-layout {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto auto 1fr 1fr;
  gap: 20px;
  grid-template-areas:
    "tabs tabs tabs"
    "created redeemed stats"
    "latest pending top"
    "latest inactive top";
}

/* Dashboard Controls (Tabs & Search) */
.dashboard-controls {
  grid-area: tabs;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.dashboard-tabs {
  display: flex;
  gap: 10px;
}
.tab-button {
  padding: 10px 20px;
  font-size: 1em;
  font-weight: 600;
  border: none;
  border-radius: 6px;
  background-color: #30333d;
  color: #a0a5b1;
  cursor: pointer;
  transition: all 0.2s ease;
}
.tab-button.active {
  background-color: #3498db;
  color: #fff;
}
.tab-button:hover:not(.active) {
  background-color: #4a4d59;
}

.dashboard-search {
  min-width: 350px;
}
/* Style react-select within dashboard search */
.dashboard-search .css-b62m3t-container { font-size: 0.95em; }
.dashboard-search .css-13cymwt-control,
.dashboard-search .css-t3ipsp-control {
    background-color: #30333d;
    border-color: #4a4d59;
    min-height: 42px;
}
/* Dashboard Search Dropdown */
.dashboard-search .css-1nmdiq5-menu,
.dashboard-search .css-avt5d7-menu { background-color: #30333d; }
.dashboard-search .css-d7l1ni-option,
.dashboard-search .css-tr4s17-option {
  background-color: #30333d; color: #e0e0e0;
  white-space: nowrap; overflow: hidden; text-overflow: ellipsis;
}
.dashboard-search .css-1n7v3ny-option,
.dashboard-search .css-1234789-option { /* Adjust focused class if needed */
  background-color: #3498db; color: #ffffff;
}

/* Dashboard Stat Cards */
.stat-card {
  &.created-card { grid-area: created; }
  &.redeemed-card { grid-area: redeemed; }
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 24px;
}
.stats-breakdown {
  grid-area: stats;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.stat-icon { font-size: 2.5em; padding: 12px; border-radius: 50%; }
.created-card .stat-icon { color: #f39c12; background-color: rgba(243, 156, 18, 0.1); }
.redeemed-card .stat-icon { color: #2ecc71; background-color: rgba(46, 204, 113, 0.1); }
.stat-info .stat-label { font-size: 0.9em; color: #a0a5b1; margin-bottom: 4px; }
.stat-info .stat-big-number { font-size: 2.2em; font-weight: 700; color: #fff; line-height: 1.1; }
.stat-info .stat-subtext { font-size: 0.9em; color: #a0a5b1; }

/* Dashboard List Cards */
.list-card {
  &.latest-redemptions { grid-area: latest; }
  &.pending-coupons { grid-area: pending; }
  &.top-redeemers { grid-area: top; }
  &.inactive-customers { grid-area: inactive; }
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Shared Card Header/Body Styles */
.card-header {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 16px 20px;
  border-bottom: 1px solid #30333d;
  flex-shrink: 0;
}
.card-header .card-icon { color: #a0a5b1; font-size: 1.2em; }
.card-header h3 { margin: 0; font-size: 1.1em; font-weight: 600; color: #e0e0e0; }
.card-body.scrollable-list {
  flex-grow: 1;
  overflow-y: auto;
  padding: 0 20px 20px 20px;
}

/* Shared Info Card Styles (used inside lists) */
.info-card {
  background-color: #2a2d35;
  border: 1px solid #4a4d59;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 10px; /* Overridden by .coupon-list-item if needed */
}
.info-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1em;
  font-weight: 600;
  padding-bottom: 8px;
  border-bottom: 1px solid #4a4d59;
}
/* Adjust info-card-header for tier badge */
.info-card-header button.clickable-name { flex-grow: 0; margin-right: auto; }

.info-card-highlight { color: #ffa500; font-weight: 700; font-size: 1em; }
.info-card-details { list-style: none; padding: 0; margin: 10px 0 0 0; font-size: 0.9em; color: #a0a5b1; }
.info-card-details li { display: flex; justify-content: space-between; align-items: center; margin-bottom: 5px; }
.info-card-details li span { display: flex; align-items: center; gap: 6px; }
.info-card-details li strong { color: #e0e0e0; font-weight: 600; }
.info-card-details li.divider { height: 1px; background-color: #4a4d59; margin: 8px 0; padding: 0; list-style: none; }

/* Clickable Name Style */
.clickable-name {
  background: none; border: none; padding: 0; margin: 0; font-family: inherit;
  font-size: inherit; font-weight: 600; color: #3498db; cursor: pointer; text-align: left;
}
.clickable-name:hover { text-decoration: underline; color: #5dade2; }

/* Stats Breakdown List Styles */
.stat-group { margin-bottom: 15px; }
.stat-group > strong { display: flex; align-items: center; gap: 8px; font-size: 1em; font-weight: 600; margin-bottom: 10px; }
.stat-breakdown-list .list-item-small { display: flex; justify-content: space-between; font-size: 0.9em; padding: 5px 8px; border-radius: 4px; }
.stat-breakdown-list .list-item-small:nth-child(odd) { background-color: #2a2d35; }
.stat-breakdown-list .text-muted { font-size: 0.9em; color: #a0a5b1; }

/* ========================================= */
/* --- REPORT & FILTER STYLES --- */
/* ========================================= */
.report-page h2 { margin-bottom: 20px; } /* For Staff/Tier reports */

/* Professional Filter Section */
.filter-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 15px;
  padding: 20px 25px;
}
.filter-section .form-input,
.filter-section .form-select,
.filter-section .react-datepicker-wrapper .form-input {
  background-color: #30333d; color: #e0e0e0; border: 1px solid #4a4d59;
  border-radius: 6px; padding: 10px 14px; font-size: 1em; font-family: inherit;
  height: 42px; margin: 0;
}
.filter-section .form-input:hover,
.filter-section .form-select:hover { border-color: #3498db; }
.filter-section .form-input:focus,
.filter-section .form-select:focus { outline: none; border-color: #3498db; }
.filter-section .react-datepicker-wrapper .form-input { width: 140px; }
.filter-section .search-input { flex-grow: 1; min-width: 200px; }
.filter-section .form-select { min-width: 150px; }
.filter-section .search-btn-aligned { height: 42px; padding: 10px 20px; margin-left: auto; }

/* Summary Bar */
.summary-bar {
  display: flex;
  justify-content: space-around;
  padding: 15px 25px;
  font-weight: 600;
  font-size: 1.1em;
  background-color: #2a2d35;
}
.summary-bar div strong { color: #a0a5b1; margin-right: 8px; }

/* Report Table Styles */
.report-table-card .card-body { padding: 0; }
.report-table { width: 100%; border-collapse: collapse; }
.report-table th, .report-table td { padding: 12px 15px; text-align: left; border-bottom: 1px solid #30333d; }
.report-table th { background-color: #2a2d35; font-weight: 600; color: #e0e0e0; }
.report-table tbody tr:hover { background-color: #2a2d35; }
/* Right-align numeric data */
.report-table td:nth-child(n+2),
.report-table th:nth-child(n+2) { text-align: right; }
/* Report table row indentation */
.report-table tbody tr.indented-row td:first-child { padding-left: 30px; }
.report-table tbody tr.primary-row .clickable-name { font-weight: 600; }

/* Status Colors */
td.status-redeemed { color: #27ae60; font-weight: 600; }
td.status-pending { color: #f39c12; font-weight: 600; }
td.status-expired { color: #e74c3c; font-weight: 600; text-decoration: line-through; }

/* ========================================= */
/* --- CUSTOMER TIER BADGES --- */
/* ========================================= */
.tier-badge {
  font-size: 0.8em; font-weight: 600; padding: 3px 8px; border-radius: 4px;
  margin-left: 10px; color: #fff; text-transform: uppercase; vertical-align: middle;
}
.tier-bronze { background-color: #cd7f32; }
.tier-silver { background-color: #c0c0c0; color: #333; }
.tier-gold { background-color: #ffd700; color: #333; }

/* ========================================= */
/* --- GENERAL FORM STYLES --- */
/* (Used by Settings, Modals etc.) */
/* ========================================= */
/* Style for text inputs and number inputs */
.form-input,
.form-select {
  background-color: #30333d; color: #e0e0e0; border: 1px solid #4a4d59;
  border-radius: 6px; padding: 10px 14px; font-size: 1em; font-family: inherit;
  height: 42px; /* Ensure consistent height */
  margin: 0; /* Remove default margins */
}
.form-input:hover,
.form-select:hover { border-color: #3498db; }
.form-input:focus,
.form-select:focus { outline: none; border-color: #3498db; }
.form-select { flex-grow: 1; } /* Allow selects to grow if needed */

/* Modal Styles (Basic Overlay + Content) */
.modal-overlay {
  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex; justify-content: center; align-items: center; z-index: 1000;
}
.modal-content {
  background-color: #23252e; border-radius: 8px; padding: 30px;
  min-width: 350px; max-width: 90%;
  border: 1px solid #30333d;
}
.modal-content h4 { margin-top: 0; font-size: 1.4em; color: #fff; margin-bottom: 20px; }
.modal-content p { color: #a0a5b1; margin-bottom: 25px; }
.modal-actions { display: flex; justify-content: flex-end; gap: 15px; margin-top: 30px; }
.btn-cancel, .btn-confirm {
   padding: 10px 20px; font-size: 1em; font-weight: 600; border: none; border-radius: 6px; cursor: pointer;
}
.btn-cancel { background-color: #4a4d59; color: #e0e0e0; }
.btn-cancel:hover { background-color: #5a5d6a; }
.btn-confirm { background-color: #3498db; color: #fff; }
.btn-confirm:hover { background-color: #2980b9; }

/* Styles for Admin Tool (CustomerSearch) buttons */
.btn-add, .btn-void {
  padding: 8px 15px; font-size: 0.9em; font-weight: 500; border: none; border-radius: 6px; cursor: pointer;
  display: inline-flex; align-items: center; gap: 6px; margin-left: 5px; /* Adjust spacing */
}
.btn-add { background-color: #27ae60; color: #fff; } /* Green */
.btn-add:hover { background-color: #229954; }
.btn-void { background-color: #e74c3c; color: #fff; } /* Red */
.btn-void:hover { background-color: #c0392b; }
/* --- LOGIN PAGE STYLES (Add to App.css) --- */
/* --- LOGIN PAGE STYLES (Update this block in App.css) --- */
.login-page-container {
  display: flex;
  /* CRITICAL FIX: Align center horizontally and vertically */
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Use full screen height */
  background-color: #1a1c23;
  /* Ensure no other margins are pushing it around */
  margin: 0;
  padding: 0;
}
/* --- The rest of the login styles remain unchanged --- */
.login-form {
  width: 100%;
  max-width: 380px; /* Standard login box size */
  padding: 30px;
  text-align: center;
  background-color: #23252e;
}
.login-header {
  margin-bottom: 25px;
}
.login-logo-icon {
  font-size: 2.5rem;
  color: #3498db;
  margin-bottom: 10px;
}
.login-form h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: #ffffff;
}
.login-form .form-group {
    text-align: left;
}
.login-form .form-group label {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 5px;
    font-weight: 500;
    color: #a0a5b1;
}
.login-form button[type="submit"] {
    width: 100%;
    margin-top: 15px;
}
.register-text {
    font-size: 0.9em;
    color: #7f8c8d;
    margin-top: 20px;
}
@keyframes spin { 0% { transform: rotate(0deg); } 100% { transform: rotate(360deg); } }
.spin { animation: spin 1s linear infinite; }
/* --- END LOGIN PAGE STYLES --- */