.sidebar {
  width: 260px;
  background-color: #23252e; /* Slightly lighter dark */
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  border-right: 1px solid #30333d;
}

.sidebar-header {
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 1.5rem;
  font-weight: 600;
  color: #ffffff;
  padding: 20px;
  border-bottom: 1px solid #30333d;
}

.sidebar-menu {
  list-style: none;
  flex-grow: 1;
  padding-top: 15px;
}

.sidebar-menu li {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px 20px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #a0a5b1;
  cursor: pointer;
  transition: all 0.2s ease;
  border-left: 3px solid transparent;
}

.sidebar-menu li:hover {
  background-color: #30333d;
  color: #ffffff;
  border-left-color: #3498db;
}

.sidebar-footer {
  padding: 20px;
  text-align: center;
  font-weight: 500;
  color: #a0a5b1;
  border-top: 1px solid #30333d;
  cursor: pointer;
}

.sidebar-footer:hover {
  color: #e74c3c;
}

/* --- New CSS for the Separator --- */
.sidebar-separator {
  padding: 8px 15px 5px; /* Top, horizontal, bottom padding */
  margin-top: 15px;      /* Space above the section */
  margin-bottom: 5px;    /* Space below the title */
  font-size: 0.85em;     /* Smaller font size */
  font-weight: 700;      /* Bold font */
  color: #adb5bd;        /* Lighter color for section title */
  text-transform: uppercase;
  letter-spacing: 0.5px;
  cursor: default !important; /* Cannot be clicked */
  background: none !important; /* Ensure no hover background */
}
/* General list item hover/click effect */
.sidebar-menu li:hover {
  background-color: #3a3f47;
}
.sidebar-menu li.sidebar-separator:hover {
  background: none; /* Override hover effect for the separator */
}

/* --- NEW CSS for Scrollable Sidebar Menu --- */

.sidebar {
  /* Set a fixed width */
  width: 250px; 
  flex-shrink: 0;
  background-color: #2c2f38; 
  color: #e0e0e0;
  display: flex;
  flex-direction: column;
  height: 100vh; /* Ensure it fills the screen height */
  overflow: hidden; /* Prevent sidebar itself from showing scrollbar */
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1; /* Allow menu area to take remaining space */
  overflow-y: auto; /* --- FIX: Add Vertical Scrollbar to Menu List --- */
  overflow-x: hidden;
  /* Apply padding inside the list area */
  padding-bottom: 20px; 
}

/* Ensure the header and footer do not scroll */
.sidebar-header,
.sidebar-footer {
    flex-shrink: 0; 
}