/* --- CustomerDashboard.css --- */

/* Styles specific to the customer dashboard card */
.customer-dashboard .card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #30333d;
  padding: 20px 25px;
  margin-bottom: 20px;
}

.customer-dashboard .card-header h2 {
  margin: 0;
  color: #ffffff;
}

.clear-btn {
  background: none;
  border: none;
  color: #95a5a6;
  font-size: 1.25rem;
  cursor: pointer;
  padding: 5px;
  line-height: 1;
  border-radius: 50%;
  transition: color 0.2s ease, background-color 0.2s ease;
}

.clear-btn:hover {
  color: #e74c3c; /* Red on hover */
  background-color: #443034; /* Dark red background */
}

/* Customer Details Layout */
.customer-details-body {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  padding: 0 25px 25px 25px;
}

.customer-info {
  flex-grow: 1;
}

.detail-row {
  font-size: 1rem;
  padding: 10px 0;
  display: flex;
  border-bottom: 1px dashed #30333d;
}
.detail-row:last-child { border-bottom: none; }

.detail-row strong {
  min-width: 140px;
  color: #a0a5b1;
  font-weight: 500;
}
.detail-row span {
  font-weight: 500;
  color: #ffffff;
}

.coupon-highlight-box {
  background: linear-gradient(135deg, #27ae60, #2ecc71); /* Green gradient */
  color: white;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  min-width: 200px;
}
.coupon-icon { font-size: 2.5rem; opacity: 0.8; }
.coupon-count { font-size: 3.5rem; font-weight: 700; line-height: 1.2; }
.coupon-label { font-size: 1.1rem; font-weight: 500; opacity: 0.9; }

/* Customer Header (Name + Tier Badge) */
.customer-header-info {
    display: flex;
    align-items: center; /* Vertically align name and badge */
}
.customer-header-info h2 {
    margin: 0; /* Remove default margin */
}

/* Find this class */
.coupon-list-container {
  /* Let this container take up all the vertical space */
  flex-grow: 1; 
  display: flex;
  flex-direction: column;
  overflow-y: hidden; /* Prevent this section from scrolling */
  padding: 0 25px 20px 25px;
  border-bottom: 1px solid #30333d;
}

/* Target the actual list wrapper for scrolling */
.scrollable-coupon-list {
  flex-grow: 1; /* CRITICAL: Takes up remaining height */
  overflow-y: auto; /* CRITICAL: Allows scrolling ONLY in the list area */
  max-height: none; /* Override max-height if set, rely on flex-grow */
  padding: 5px 10px 10px 5px; 
  border: 1px solid #30333d; 
  border-radius: 6px;
  background-color: #1a1c23; 
}

.coupon-list-container h3 {
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 15px;
}

/* Scrollable list area for coupons */
.scrollable-coupon-list {
  max-height: 200px; /* Adjust max height as needed */
  overflow-y: auto;
  padding: 5px 10px 10px 5px; /* Padding inside the scroll area */
  border: 1px solid #30333d; /* Border around scroll area */
  border-radius: 6px; /* Rounded corners */
  background-color: #1a1c23; /* Darker inner background */
}

.coupon-list-item {
  margin-bottom: 8px; /* Spacing between items in the list */
}

/* Expiry Warning Styles */
.expiry-warning {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.9em;
  font-weight: 500;
  color: #a0a5b1; /* Muted color by default */
}

.expiry-warning.visible {
  color: #e74c3c; /* Red warning color */
  font-weight: 600;
}

/* --- Cart Style Redeem Section --- */
.redeem-container.cart-style {
  padding: 20px 25px; /* Match customer-details-body padding */
  border-top: 1px solid #30333d; /* Add separator if needed */
}

.redeem-container.cart-style h3 {
  margin-top: 0;
  font-weight: 600;
  color: #ffffff;
  margin-bottom: 15px;
}

.add-buttons-container {
  display: flex;
  gap: 15px;
  margin-bottom: 25px; /* Space before cart summary */
}

.add-cart-btn {
  flex-grow: 1; /* Make buttons fill space */
  justify-content: center; /* Center icon and text */
}

/* Cart Summary Styling */
.cart-summary {
  margin-top: 0; /* Remove extra margin */
  padding: 15px;
  background-color: #2a2d35;
  border: 1px solid #4a4d59;
  border-radius: 6px;
  margin-bottom: 20px; /* Space before finalize */
}

.cart-summary h4 {
  margin-top: 0;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid #4a4d59;
  font-size: 1.1em;
  font-weight: 600;
  color: #e0e0e0;
}

.cart-items {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 15px; /* Space before total */
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1em;
  font-weight: 500;
}

.cart-item span {
  display: flex;
  align-items: center;
  gap: 8px;
}

.remove-item-btn { /* Style for the trash icon button */
  background: none;
  border: none;
  color: #e74c3c; /* Red color for delete */
  cursor: pointer;
  font-size: 1.1em;
  padding: 5px; /* Add some padding */
  line-height: 1; /* Ensure icon centers */
}
.remove-item-btn:hover { color: #c0392b; } /* Darker red */

.remove-item-btn.small-btn {
  padding: 3px 6px; /* Smaller padding */
  font-size: 0.9em; /* Smaller icon */
}

.cart-total {
  margin-top: 0; /* Remove extra margin */
  font-size: 1.2em;
  font-weight: 700;
  color: #e0e0e0;
  text-align: right;
}

/* Horizontal Divider */
hr.divider {
  border: none;
  border-top: 1px solid #4a4d59;
  margin: 20px 0;
}

/* Finalize Form Layout */
.finalize-form {
  display: flex;
  gap: 15px;
  align-items: center;
}

.finalize-form .form-select {
  flex-grow: 0; /* Don't let it grow */
  min-width: 150px; /* Give it a minimum width */
}

