.history-search-bar {
  display: flex;
  gap: 15px;
}
.history-search-bar input {
  flex-grow: 1;
  font-size: 1.1rem;
  padding: 12px 15px;
  background-color: #30333d;
  border: 1px solid #4a4d59;
  border-radius: 6px;
  color: #e0e0e0;
}
.history-search-bar input:focus {
  outline: none;
  border-color: #3498db;
}
.history-search-bar button {
  font-size: 1rem;
  font-weight: 500;
  padding: 10px 20px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
}

.history-results h3 {
  font-size: 1.4rem;
  margin-bottom: 20px;
  border-bottom: 1px solid #30333d;
  padding-bottom: 15px;
}
.no-history {
  text-align: center;
  font-size: 1.2rem;
  color: #7f8c8d;
  padding: 40px;
}

