/* --- New 4-column horizontal layout --- */
.settings-horizontal-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 equal columns */
  gap: 20px;
}


/* --- Styles for forms INSIDE a card --- */
/* (We re-use .card, .card-header, .card-body from App.css) */

.settings-horizontal-layout .card-body {
  padding: 20px; /* Add padding inside card bodies */
}

.settings-horizontal-layout .form-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align labels left */
  gap: 8px;
  width: 100%;
  margin-bottom: 25px; /* Add margin between groups */
}
.settings-horizontal-layout .card-body > .form-group:last-child {
  margin-bottom: 0;
}

.settings-horizontal-layout .form-group label {
  text-align: left; /* Align labels left */
  font-size: 1rem;
  font-weight: 600;
  color: #e0e0e0;
}

.settings-horizontal-layout .form-group p {
  font-size: 0.85rem;
  color: #a0a5b1;
  margin: 0;
}

.settings-horizontal-layout .form-input,
.settings-horizontal-layout .form-select {
  width: 100%; /* Make inputs fill the space */
}

/* --- Clean up old, unused styles --- */
.settings-container,
.settings-container .card-header,
.settings-form,
.form-actions,
.form-actions .tab-button,
.form-actions button,
.form-actions button:hover,
.form-actions button:disabled,
.tier-settings-box,
.tier-settings-box h3,
.general-settings-box,
.general-settings-box h3,
.settings-form-grid,
.settings-form-tiered {
  /* Remove all old layout styles */
  all: unset;
}